package com.example.project

import app.cash.sqldelight.SuspendingTransacter
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.example.AppDbQueries
import com.example.project.shared.newInstance
import com.example.project.shared.schema
import kotlin.Unit

public interface AppDb : SuspendingTransacter {
  public val appDbQueries: AppDbQueries

  public companion object {
    public val Schema: SqlSchema<QueryResult.AsyncValue<Unit>>
      get() = AppDb::class.schema

    public operator fun invoke(driver: SqlDriver): AppDb = AppDb::class.newInstance(driver)
  }
}
