package com.example

import app.cash.sqldelight.Query
import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Long
import kotlin.String
import kotlin.collections.Collection

public class AppDbQueries(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver) {
  public fun <T : Any> selectAllLaunchesInfo(mapper: (
    flightNumber: Long,
    missionName: String,
    details: String?,
    launchSuccess: Long?,
    launchDateUTC: String,
    patchUrlSmall: String?,
    patchUrlLarge: String?,
    articleUrl: String?,
    timestamp: String,
  ) -> T): Query<T> = Query(1_818_151_236, arrayOf("Launch"), driver, "AppDb.sq",
      "selectAllLaunchesInfo", """
  |SELECT Launch.*
  |FROM Launch
  """.trimMargin()) { cursor ->
    mapper(
      cursor.getLong(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2),
      cursor.getLong(3),
      cursor.getString(4)!!,
      cursor.getString(5),
      cursor.getString(6),
      cursor.getString(7),
      cursor.getString(8)!!
    )
  }

  public fun selectAllLaunchesInfo(): Query<Launch> = selectAllLaunchesInfo { flightNumber,
      missionName, details, launchSuccess, launchDateUTC, patchUrlSmall, patchUrlLarge, articleUrl,
      timestamp ->
    Launch(
      flightNumber,
      missionName,
      details,
      launchSuccess,
      launchDateUTC,
      patchUrlSmall,
      patchUrlLarge,
      articleUrl,
      timestamp
    )
  }

  public suspend fun insertLaunch(
    flightNumber: Long,
    missionName: String,
    details: String?,
    launchSuccess: Long?,
    launchDateUTC: String,
    patchUrlSmall: String?,
    patchUrlLarge: String?,
    articleUrl: String?,
    timestamp: String,
  ) {
    driver.execute(-1_322_404_836, """
        |INSERT INTO Launch(flightNumber, missionName, details, launchSuccess, launchDateUTC, patchUrlSmall, patchUrlLarge, articleUrl, timestamp)
        |VALUES(?, ?, ?, ?, ?, ?, ?, ?, ?)
        """.trimMargin(), 9) {
          bindLong(0, flightNumber)
          bindString(1, missionName)
          bindString(2, details)
          bindLong(3, launchSuccess)
          bindString(4, launchDateUTC)
          bindString(5, patchUrlSmall)
          bindString(6, patchUrlLarge)
          bindString(7, articleUrl)
          bindString(8, timestamp)
        }.await()
    notifyQueries(-1_322_404_836) { emit ->
      emit("Launch")
    }
  }

  public suspend fun removeAllLaunches() {
    driver.execute(179_229_422, """DELETE FROM Launch""", 0).await()
    notifyQueries(179_229_422) { emit ->
      emit("Launch")
    }
  }

  public suspend fun removeLaunchesByFlightNumber(flightNumber: Collection<Long>) {
    val flightNumberIndexes = createArguments(count = flightNumber.size)
    driver.execute(null, """DELETE FROM Launch WHERE flightNumber IN $flightNumberIndexes""",
        flightNumber.size) {
          flightNumber.forEachIndexed { index, flightNumber_ ->
            bindLong(index, flightNumber_)
          }
        }.await()
    notifyQueries(-222_439_995) { emit ->
      emit("Launch")
    }
  }
}
