package com.example.project.shared

import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.AfterVersion
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.example.AppDbQueries
import com.example.project.AppDb
import kotlin.Long
import kotlin.Unit
import kotlin.reflect.KClass

internal val KClass<AppDb>.schema: SqlSchema<QueryResult.AsyncValue<Unit>>
  get() = AppDbImpl.Schema

internal fun KClass<AppDb>.newInstance(driver: SqlDriver): AppDb = AppDbImpl(driver)

private class AppDbImpl(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver), AppDb {
  override val appDbQueries: AppDbQueries = AppDbQueries(driver)

  public object Schema : SqlSchema<QueryResult.AsyncValue<Unit>> {
    override val version: Long
      get() = 1

    override fun create(driver: SqlDriver): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
      driver.execute(null, """
          |CREATE TABLE IF NOT EXISTS  Launch (
          |    flightNumber INTEGER NOT NULL,
          |    missionName TEXT NOT NULL,
          |    details TEXT,
          |    launchSuccess INTEGER DEFAULT NULL,
          |    launchDateUTC TEXT NOT NULL,
          |    patchUrlSmall TEXT,
          |    patchUrlLarge TEXT,
          |    articleUrl TEXT,
          |    timestamp TEXT NOT NULL
          |)
          """.trimMargin(), 0).await()
    }

    override fun migrate(
      driver: SqlDriver,
      oldVersion: Long,
      newVersion: Long,
      vararg callbacks: AfterVersion,
    ): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
    }
  }
}
